@font-face {
	font-family: 'proximanova-light';
	src: url('../fonts/ProximaNova-Light.otf') format('truetype');
	font-weight: normal;
	font-style: normal;
}
@font-face {
	font-family: 'proximanova-black';
	src: url('../fonts/ProximaNova-Black.otf') format('truetype');
	font-weight: normal;
	font-style: normal;
}
@font-face {
	font-family: 'proximanova-reg';
	src: url('../fonts/ProximaNova-Reg.otf') format('truetype');
	font-weight: normal;
	font-style: normal;
}
@font-face {
	font-family: 'proximanova-bold';
	src: url('../fonts/ProximaNova-Bold.otf') format('truetype');
	font-weight: normal;
	font-style: normal;
}
@font-face {
	font-family: "museosansbold";
	src: url("../fonts/MuseoSansBold.eot");
	src: url("../fonts/MuseoSansBold.eot?#iefix")format("embedded-opentype"),
	url("../fonts/MuseoSansBold.woff") format("woff"),
	url("../fonts/MuseoSansBold.ttf") format("truetype");
	font-style: normal;
	font-weight: normal;
}